import { Component } from "react";
import { Route, Redirect } from "react-router-dom";

export default function ProtectedRoute({
  isAuth,
  component: Component,
  ...rest
}) {
  return (
    <Route
      {...rest}
      render={(props) => {
        if (isAuth) {
          <Component />;
        } else {
          return (
            <Redirect
              to={{ pathname: "/auth", state: { from: props.location } }}
            />
          );
        }
      }}
    />
  );
}
