import Footer from "../footer";
import {
  FaStopwatch,
  FaTractor,
  FaWater,
  FaShoppingCart,
} from "react-icons/fa";
import MeetTheTeam from "./our-team";
import { useContext } from "react";
import { AppContext } from "../../api/store/context";
export default function AboutUs() {
  const { legals, aboutus } = useContext(AppContext);
  return (
    <div className="vw-100 vh-100" style={{ marginTop: "70px" }}>
      <div className="container mt-4">
        <div className="row justify-content-center">
          <div className="col-sm-10">
            <h2 className="display-4 text-center light-brand-color">
              About Jadeja
            </h2>
          </div>
          {/* <div className="col-sm-8 border-under hard-brand-background"></div> */}
        </div>
      </div>
      {/* ========== end of the heading ========= */}

      <div className="container pt-3">
        <div className="row justify-content-center py-3">
          <p
            className="lead col-sm-10 text-center"
            style={{ fontSize: "1.7rem" }}
          >
            {aboutus}
          </p>
        </div>
      </div>
      {/* end of the title */}
      <div className="container py-2">
        <div className="row">
          <div className="col-md-6 p-3">
            <p className="font-weight-bold light-brand-color text-center h3">
              We're legal registered company under the act of AAA-XXX Corporate
            </p>
            <p className="lead">
              Law is the body of law governing the rights, relations, and
              conduct of persons, companies, organizations and businesses. The
              term refers to the legal practice of law relating to corporations,
              or to the theory of corporations
            </p>
            {legals.map((lg, idx) => (
              <div key={idx}>
                <h4 className="light-brand-color simple-title position-relative">
                  {lg.title}
                </h4>
                <div className="simple-titled-content position-relative pl-4">
                  <p className="mb-0">{lg.value}</p>
                </div>
              </div>
            ))}
          </div>
          <div className="col-md-6">
            <div className="row gutters-2">
              <div className="col-sm-5 m-1 shadow">
                <div className="w-100">
                  <span className="light-brand-color bigger-icon-size">
                    <FaTractor />
                  </span>
                </div>
                <p className="h4 hard-brand-color font-weight-bold">
                  Modern Practice
                </p>
                <p className="lead font-size-normal">
                  Wakati sahihi kwa kilimo na viwanda, kutumika kama kinyenyua
                  uchumi kwa watanzania pamoja na ukuaji wa biashara ya mazao
                </p>
              </div>

              <div className="col-sm-5 m-1 hard-brand-background text-light shadow">
                <div className="w-100">
                  <span className="bigger-icon-size">
                    <FaWater />
                  </span>
                </div>
                <p className="h4  text-light font-weight-bold">
                  Irrigation Farming
                </p>
                <p className="lead font-size-normal">
                  Wakati sahihi kwa kilimo na viwanda, kutumika kama kinyenyua
                  uchumi kwa watanzania pamoja na ukuaji wa biashara ya mazao
                </p>
              </div>

              <div className="col-sm-5 m-1 hard-brand-background text-light shadow">
                <div className="w-100">
                  <span className="bigger-icon-size">
                    <FaStopwatch />
                  </span>
                </div>
                <p className="h4  text-light font-weight-bold">
                  Tractor harvesting
                </p>
                <p className="lead font-size-normal">
                  Wakati sahihi kwa kilimo na viwanda, kutumika kama kinyenyua
                  uchumi kwa watanzania pamoja na ukuaji wa biashara ya mazao
                </p>
              </div>

              <div className="col-sm-5 m-1 shadow ">
                <div className="w-100">
                  <span className="light-brand-color bigger-icon-size">
                    <FaShoppingCart />
                  </span>
                </div>
                <p className="h4 hard-brand-color font-weight-bold">
                  Market searching
                </p>
                <p className="lead font-size-normal">
                  Wakati sahihi kwa kilimo na viwanda, kutumika kama kinyenyua
                  uchumi kwa watanzania pamoja na ukuaji wa biashara ya mazao
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <MeetTheTeam />
      <Footer />
    </div>
  );
}
