import { useContext } from "react";
import { BASE_URL_SRC } from "../../api/server";
import { AppContext } from "../../api/store/context";

const MeetTheTeam = (props) => {
  const { staffsLoading, staffs } = useContext(AppContext);

  return (
    <div className="bg-light pt-3 mt-5">
      <div className="container mb-2">
        <div className="row justify-content-center py-4">
          <h3 className="col-sm-5 simple-title text-center display-4 light-brand-color">
            Meet Our Team
          </h3>
        </div>
      </div>
      {/* end of title */}
      {staffsLoading ? (
        <p className="light-brand-color display-3 text-center">
          Please wait...
        </p>
      ) : (
        <TeamMember staffs={staffs} />
      )}
    </div>
  );
};

export default MeetTheTeam;

const TeamMember = (props) => {
  const { staffs } = props;
  return (
    <section className="container">
      {staffs.length ? (
        staffs.map((st, idx) => (
          <StuffRow
            vector={idx % 2 === 0 ? "left" : "right"}
            position={st.position}
            name={st.name}
            url={`${BASE_URL_SRC}${st.picture}`}
            {...st}
          />
        ))
      ) : (
        <p className="light-brand-color display-3 text-center">
          To Be Updated Soon...
        </p>
      )}
    </section>
  );
};

const StuffRow = (props) => {
  return (
    <div className="row no-gutters justify-content-center align-items-center pb-5">
      <div className="col-sm-5">
        <SlightedPhoto rot={props.vector} {...props} />
      </div>
      <div className="col-sm-7 light-lined-content pl-4">
        <h4 className="display-5 pale-brand-color m-1 p-0 border-bottom">
          {props.position}
        </h4>
        <h2 className="display-5 pale-brand-color m-1 p-0 border-bottom">
          {props.name}
        </h2>
        <p className="lead">{props.description}</p>
      </div>
    </div>
  );
};
const SlightedPhoto = (props) => {
  const { rot, name, position } = props;
  return (
    <div
      className={`w-75 slighted-photo-container ${
        rot === "left" ? "rotate-left" : "rotate-right"
      }`}
    >
      <img src={props.url} className="img-fluid" />
      <div className="px-2 py-2">
        <h4 className="light-brand-color px-auto">{name}</h4>
        <p className="h3 light-brand-color">{position}</p>
      </div>
    </div>
  );
};
