import { useParams } from "react-router-dom";
import { useState, useEffect } from "react";
import { BASE_URL_SRC, getThisSingleCategoryPointApi } from "../../api/server";
import Footer from "../footer";
import AlternatingCircles from "../loaders/good-animations";
const SomaZaidi = (props) => {
  const { id } = useParams();
  const [loading, setLoading] = useState(false);
  const [point, setPoint] = useState(null);
  const getThisSinglePoint = async () => {
    setLoading(true);
    try {
      if (!id) return;
      //we're cool here
      const resp = await getThisSingleCategoryPointApi(id);
      if (resp.data && resp.data.success && resp.data.contents.length) {
        setPoint(resp.data.contents[0]);
      }
    } catch (e) {
      //   console.log(e)
      point(null);
    }
    setLoading(false);
  };

  useEffect(() => {
    getThisSinglePoint();
  }, []);
  return (
    <div style={{ marginTop: "8rem" }}>
      {loading ? (
        <div
          className="d-flex flex-column justify-content-center align-items-center "
          style={{ height: "75vh" }}
        >
          <p className="light-brand-color text-center my-1 display-4">
            Please wait...
          </p>
          <AlternatingCircles />
        </div>
      ) : point ? (
        <div style={{ height: "95vh" }}>
          <div className="container">
            <div className="row">
              <div className="col-sm-4">
                <img
                  src={`${BASE_URL_SRC}${point.picture}`}
                  className="img-fluid"
                />
              </div>
              <div className="col-sm-8">
                <h4 className="light-brand-color">{point.title}</h4>
                <p className="lead">{point.description}</p>
              </div>
            </div>
          </div>
        </div>
      ) : (
        <div
          className="d-flex flex-column justify-content-center align-items-center "
          style={{ height: "75vh" }}
        >
          <p className="light-brand-color text-center my-1 display-4">
            Contents will be available soon,
          </p>
        </div>
      )}
      <Footer />
    </div>
  );
};

export default SomaZaidi;
