import { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { loginAdminApi } from "../../../api/server";
import { setAdmin, getAdmin } from "../../../api/store/local";

const AdminAuth = () => {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [loginWait, setLoginWait] = useState(false);
  const [error, setError] = useState(null);
  const history = useHistory();
  const userLogin = async () => {
    setLoginWait(true);
    try {
      const loggedUser = await loginAdminApi({ username, password });
      console.log(loggedUser);
      if (loggedUser.data.success) {
        //then we have the tokens
        setAdmin({ token: loggedUser.data.token });
        setError(null);
        history.push("/manage-staffs");
      } else {
        //then wehave an error here
        console.log("error logged here");
        setError("Either username/password didnt match");
      }
      setError(null);
    } catch (e) {
      console.log("Failed with sata");
      setError("There was an error please try again");
    }
    setLoginWait(false);
  };

  const getAdminLocal = async () => {
    try {
      const resp = await getAdmin();
      if (resp) {
        //If we have an admin at all
        history.push("/manage-staffs");
      }
    } catch (e) {
      //   console.log(e)
    }
  };
  useEffect(() => {
    const admin = getAdminLocal();
  }, []);
  return (
    <div className="" style={{ marginTop: "5rem" }}>
      <div className="container">
        <div className="row">
          <div className="col-sm-5 mx-auto">
            {error ? (
              <span className="text-center text-danger">{error}</span>
            ) : null}
            <h3 className="text-center light-brand-color">
              Jadeja Admin login
            </h3>
            <img
              src={"../../assets/logo.png"}
              alt="Jadeja"
              className="img-fluid mx-auto d-block"
              style={{ maxWidth: "180px" }}
            />
            <form>
              <div className="form-group">
                <label htmlFor="exampleInputEmail1">Username</label>
                <input
                  type="text"
                  className="form-control"
                  id="exampleInputEmail1"
                  aria-describedby="emailHelp"
                  placeholder="Enter Username"
                  value={username}
                  onChange={(e) => setUsername(e.target.value)}
                />
                <small id="emailHelp" className="form-text text-muted">
                  We'll never share your email/username with anyone else.
                </small>
              </div>
              <div className="form-group">
                <label htmlFor="exampleInputPassword1">Password</label>
                <input
                  type="password"
                  className="form-control"
                  id="exampleInputPassword1"
                  placeholder="Password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                />
              </div>

              <button
                type="submit"
                className="btn  mx-auto px-3 text-light light-brand-background"
                onClick={(e) => {
                  e.preventDefault();
                  if (loginWait) return;
                  userLogin();
                }}
              >
                {loginWait ? "Please wait..." : "Login"}
              </button>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AdminAuth;
