import { useState, useEffect, createContext } from "react";
import {
  getAllStaffs,
  getSocialMediaApi,
  getAllServices,
  getAllSlidesApi,
  getWedoApi,
  getCommitApi,
  getStatsApi,
  pullAllLegalsApi,
} from "../server";

export const AppContext = createContext({});

export const ContextProvider = (props) => {
  const [staffs, setStaffs] = useState([]);
  const [staffsLoading, setStaffLoading] = useState(false);
  const [telegram, setTelegram] = useState();
  const [twitter, setTwitter] = useState();
  const [youtube, setYoutube] = useState();
  const [facebook, setFacebook] = useState();
  const [instagram, setInstagram] = useState();
  const [socialLoading, setSocialLoading] = useState(false);
  const [aboutus, setAboutUs] = useState(null);
  const [trademark, setTradeMark] = useState(null);
  const [email, setEmail] = useState();
  const [location, setLocation] = useState();
  const [phone, setPhone] = useState();
  const [house, setHouse] = useState();
  const [street, setStreet] = useState();
  const [region, setRegion] = useState();
  //Service part
  const [services, setServices] = useState([]);
  const [loadingServices, setLoadingServices] = useState(false);
  const [slides, setSlides] = useState([]);
  const [waitSlides, setWaitSlides] = useState(false);
  const [listWedo, setListWeDo] = useState([]);
  const [listCommit, setListCommit] = useState([]);
  const [statsList, setListStats] = useState([]);
  const [legals, setLegals] = useState([]);
  const getAllSlides = async () => {
    setWaitSlides(true);
    try {
      const allSlides = await getAllSlidesApi();
      if (allSlides && allSlides.data && allSlides.data.success) {
        setSlides(allSlides.data.contents);
      }
    } catch (e) {
      // console.log(e)
      setServices([]);
    }
    setWaitSlides(false);
  };
  const getServices = async () => {
    setLoadingServices(true);
    try {
      const response = await getAllServices();
      setServices(response.data.contents);
    } catch (e) {
      //   console.log(e);
    }
    setLoadingServices(false);
  };
  const getSocialMedia = async () => {
    setSocialLoading(true);
    try {
      const response = await getSocialMediaApi();
      // console.log(response);
      if (response && response.data.success && response.data.payload) {
        setInstagram(response.data.payload[0].instagram);
        setFacebook(response.data.payload[0].facebook);
        setYoutube(response.data.payload[0].youtube);
        setTelegram(response.data.payload[0].telegram);
        setTwitter(response.data.payload[0].twitter);
        setAboutUs(response.data.payload[0].aboutus);
        setTradeMark(response.data.payload[0].trademark);
        setLocation(response.data.payload[0].location);
        setEmail(response.data.payload[0].email);
        setPhone(response.data.payload[0].phone);
        setStreet(response.data.payload[0].street);
        setHouse(response.data.payload[0].house);
        setRegion(response.data.payload[0].region);
      } else {
        setInstagram(null);
        setFacebook(null);
        setYoutube(null);
        setTelegram(null);
        setTwitter(null);
        setAboutUs(null);
        setTradeMark(null);
        setLocation(null);
        setEmail(null);
        setPhone(null);
        setStreet(null);
        setHouse(null);
        setRegion(null);
      }
    } catch (e) {
      //  console.log(e)
    }
    setSocialLoading(false);
  };
  const getWeDo = async () => {
    try {
      const resp = await getWedoApi();
      // console.log(resp);
      if (resp && resp.data && resp.data.success) {
        setListWeDo(resp.data.contents);
      }
    } catch (e) {
      // console.log(e);
      setListWeDo([]);
    }
  };
  const getCommits = async () => {
    try {
      const resp = await getCommitApi();
      // console.log(resp);
      if (resp && resp.data && resp.data.success) {
        setListCommit(resp.data.contents);
      }
    } catch (e) {
      // console.log(e);
      setListCommit([]);
    }
  };

  const getStats = async () => {
    try {
      const resp = await getStatsApi();
      // console.log(resp);
      if (resp && resp.data && resp.data.success) {
        setListStats(resp.data.contents);
      }
    } catch (e) {
      // console.log(e);
      setListStats([]);
    }
  };
  const pullAllLegals = async () => {
    try {
      const leg = await pullAllLegalsApi();
      if (leg && leg.data.success) {
        setLegals(leg.data.contents);
      }
    } catch (e) {
      //   console.log(e);
      setLegals([]);
    }
  };
  useEffect(() => {
    getAllSlides();

    getSocialMedia(); //just getting the social media right
    getStaffList();
    getServices();
    getWeDo();
    getCommits();
    getStats();
    pullAllLegals();
  }, []);
  const getStaffList = async () => {
    try {
      setStaffLoading(true);
      const response = await getAllStaffs();
      setStaffs(response.data.contents || []);
    } catch (e) {}
    setStaffLoading(false);
  };
  return (
    <AppContext.Provider
      value={{
        staffsLoading,
        staffs,
        setStaffs,
        telegram,
        twitter,
        youtube,
        facebook,
        instagram,
        socialLoading,
        aboutus,
        trademark,
        email,
        location,
        phone,
        house,
        street,
        region,
        services,
        loadingServices,
        slides,
        waitSlides,
        listWedo,
        listCommit,
        statsList,
        legals,
      }}
    >
      {props.children}
    </AppContext.Provider>
  );
};
