import { colors } from "../utils/colors";
import { NavLink } from "react-router-dom";
import { useContext } from "react";
import { AppContext } from "../../api/store/context";
const Navigation = (props) => {
  const { services, loadingServices } = useContext(AppContext);
  return (
    <nav className="navbar navbar-expand-lg  navbar-light font-weight-bold shadow pt-3 fixed-top bg-light">
      <a className="navbar-brand position-absolute" href="#">
        <NavLink to="/">
          <img
            src={"../../assets/logo.png"}
            alt="Jadeja"
            className="img-fluid"
            style={{ maxWidth: "60px" }}
          />
        </NavLink>
      </a>
      {/* <NavLink to = "/">
          <a className="navbar-brand" href="#">
            JADEJA</a>
          </NavLink> */}
      <button
        className="navbar-toggler"
        type="button"
        data-toggle="collapse"
        data-target="#navbarSupportedContent"
        aria-controls="navbarSupportedContent"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span className="navbar-toggler-icon" />
      </button>
      <div className="collapse navbar-collapse" id="navbarSupportedContent">
        <ul className="navbar-nav ml-auto">
          <li className="nav-item mx-2">
            <NavLink to="/">
              <a className="nav-link navigation-link navigation-color" href="#">
                Home <span className="sr-only">(current)</span>
              </a>
            </NavLink>
          </li>
          <li className="nav-item mx-2">
            <NavLink to="/about">
              <a
                className="nav-link text-decoration-none navigation-link navigation-color"
                href="#"
              >
                About us
              </a>
            </NavLink>
          </li>
          <li className="nav-item mx-2">
            <NavLink to="/contact">
              <a
                className="nav-link text-decoration-none navigation-link navigation-color"
                href="#"
              >
                Contact us
              </a>
            </NavLink>
          </li>

          <li className="nav-item dropdown mx-2">
            <a
              className="nav-link dropdown-toggle navigation-color"
              href="#"
              id="navbarDropdown"
              role="button"
              data-toggle="dropdown"
              aria-haspopup="true"
              aria-expanded="false"
            >
              Huduma zetu
            </a>
            <div
              className="dropdown-menu shadow"
              aria-labelledby="navbarDropdown"
              style={{
                backgroundColor: colors.brandLight,
                color: colors.white,
              }}
            >
              {loadingServices ? (
                <a className="dropdown-item" href="#">
                  Please wait...
                </a>
              ) : (
                services.map((serv) => (
                  <NavLink to={`/service/${serv.id}`}>
                    <a className="dropdown-item" href="#">
                      {serv.name}
                    </a>
                  </NavLink>
                ))
              )}

              {/* <NavLink to="/masoko">
                <a className="dropdown-item" href="#">
                  Masoko
                </a>
              </NavLink> */}
              {/* <div className="dropdown-divider" /> */}
            </div>
          </li>
          <li className="nav-item">
            <a className="nav-link disabled" href="#"></a>
          </li>
        </ul>
        {/* <form className="form-inline my-2 my-lg-0">
          <input
            className="form-control mr-sm-2"
            type="search"
            placeholder="Search"
            aria-label="Search"
          />
          <button
            className="btn btn-outline-success my-2 my-sm-0"
            type="submit"
          >
            Search
          </button>
        </form> */}
      </div>
    </nav>
  );
};

export default Navigation;
