import Axios from "axios";

export const BASE_URL = "http://3.11.75.1:5050/";
export const BASE_URL_SRC = "http://3.11.75.1:5050";

export const postStaffs = async (payload) => {
  return Axios.post(`${BASE_URL}users`, payload, {
    headers: {
      "Content-Type": "multipart/form-data",
    },
  });
};

/**Getting back all staffs list */
export const getAllStaffs = async () => {
  return Axios.get(`${BASE_URL}users`);
};

export const postService = async (service) => {
  return Axios.post(`${BASE_URL}service`, service);
};
export const deleteStaffApi = async (id) => {
  return Axios.delete(`${BASE_URL}users/delete/${id}`);
};

export const getAllServices = async () => {
  return Axios.get(`${BASE_URL}services`);
};

export const deleteServiceRecord = async (id) => {
  return Axios.delete(`${BASE_URL}service/${id}`);
};

export const getSingleServiceApi = async (id) => {
  return Axios.get(`${BASE_URL}service/${id}`);
};

export const editServiceApi = async (name, id) => {
  return Axios.put(`${BASE_URL}service/${id}`, { name });
};

