import "bootstrap/dist/css/bootstrap.min.css";
import HomeCarousel from "./app/home";
import Navigation from "./app/navigation/index";
import "./assets/styles/index.css";
import "jquery/dist/jquery";
import Popper from "popper.js";
import "bootstrap/dist/js/bootstrap.bundle.min";
import AboutUs from "../src/app/about/index";
import ContactUs from "../src/app/contact/index";
import Error404 from "../src/app/404/index";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import HomeSlides from "./app/home/new-slide-show";
import KilimoPage from "./app/services/kilimo";
import MasokoPage from "./app/services/masoko";
import ViwandaPage from "./app/services/viwanda";
import PostStaff from "./app/dashboard/staffs/add-staffs";
import ManageServices from "./app/dashboard/services/manage-services";
import AddServiceCategory from "./app/dashboard/service-category/add-category";
import CategoryPoint from "./app/dashboard/category-point/categoryPoint";
import "./App.css";
import SocialMediaManage from "./app/dashboard/social-media/social-media";
import SlideShowForm from "./app/dashboard/slide-show/slide-show";
import { ContextProvider } from "./api/store/context";
import SomaZaidi from "./app/services/soma-zaidi";
import LegalManager from "./app/dashboard/legals/legals-manager";
import AdminAuth from "./app/dashboard/admin/login-register";
import ProtectedRoute from "./app/authRedi/redir";
import WeDoCommitsPicture from "./app/dashboard/wedo-partners-commit/wedo-commit";

function App() {
  return (
    <ContextProvider>
      <Router>
        <Navigation />
        <Switch>
          {/* <Route path = "/" exact component = {HomeCarousel} /> */}
          <Route path="/" exact component={HomeSlides} />
          <Route path="/about" exact component={AboutUs} />
          <Route path="/contact" exact component={ContactUs} />
          <Route path="/service/:id" component={KilimoPage} />
          <Route path="/masoko" component={MasokoPage} />
          <Route path="/viwanda" component={ViwandaPage} />
          {/* ========== Protected routes ========== */}
          <Route path="/manage-services" component={ManageServices} />
          <Route path="/manage-staffs" component={PostStaff} />
          <Route path="/add-service-category" component={AddServiceCategory} />
          <Route path="/manage-social" component={SocialMediaManage} />
          <Route path="/manage-point/:id" component={CategoryPoint} />
          <Route path="/slide-show" component={SlideShowForm} />
          <Route path="/soma-zaidi/:id" component={SomaZaidi} />
          <Route path="/legal-manager" component={LegalManager} />
          <Route path="/auth" component={AdminAuth} />
          <ProtectedRoute path="/sizo" component={PostStaff} isAuth={false} />
          {/* <ProtectedRoute
            path="/wedo-manager"
            component={WeDoCommitsPicture}
            isAuth={true}
          /> */}
          <Route
            path="/wedo-manager"
            component={WeDoCommitsPicture}
            isAuth={true}
          />

          <Route path="*" component={Error404} />
        </Switch>
      </Router>
    </ContextProvider>
  );
}

export default App;
