import { useState, useEffect } from "react";
import {
  pullAllLegalsApi,
  postLegalApi,
  deleteLegalApi,
} from "../../../api/server";
import Footer from "../../footer";
import Nav from "../nav";
import Terms from "./terms";
const LegalManager = (props) => {
  const [title, setTitle] = useState();
  const [value, setValue] = useState();
  const [waitLegalPost, setWaitLegalPost] = useState(false);
  const [legals, setLegals] = useState([]); //All the required legal values
  const [waitLegalDelete, setWaitLegalDelete] = useState({
    delete: false,
    id: null,
  });
  const deleteLegal = async (id) => {
    //id of the legal to be deleted
    setWaitLegalDelete({ delete: true, id });
    try {
      await deleteLegalApi(id);
    } catch (e) {
      //   console.log(e);
    }
    setWaitLegalDelete({ delete: false, id });
  };
  const postNewLegal = async () => {
    if (!value || !title) return;
    setWaitLegalPost(true);
    try {
      const resp = await postLegalApi({ value, title });
    } catch (e) {
      //   console.log(e);
    }
    pullAllLegals();
    setWaitLegalPost(false);
  };

  const pullAllLegals = async () => {
    try {
      const leg = await pullAllLegalsApi();
      if (leg && leg.data.success) {
        setLegals(leg.data.contents);
      }
    } catch (e) {
      //   console.log(e);
      setLegals([]);
    }
  };
  useEffect(() => {
    if (!waitLegalDelete.delete && waitLegalDelete.id) {
      pullAllLegals();
    }
  }, [waitLegalPost, waitLegalDelete.delete]); //this is called only when we update the shit up men
  useEffect(() => {
    pullAllLegals();
  }, []);
  return (
    <div style={{ marginTop: "5rem" }}>
      <Nav />
      <div className="container">
        <div className="row">
          <div className="col-sm-6">
            <h5>Create New Legal</h5>
            <form className="">
              <div className="form-group mb-2">
                <label htmlFor="inputPassword2">Legal Title</label>
                <input
                  type="text"
                  className="form-control"
                  id="inputPassword2"
                  placeholder="Legal Title"
                  value={title}
                  onChange={(e) => setTitle(e.target.value)}
                />
              </div>
              <div className="form-group mb-2">
                <label htmlFor="inputPassword2">Legal Value</label>
                <input
                  type="text"
                  className="form-control"
                  id="inputPassword2"
                  placeholder="Legal value"
                  value={value}
                  onChange={(e) => setValue(e.target.value)}
                />
              </div>
              <button
                type="submit"
                className="btn btn-success mb-2"
                onClick={(e) => {
                  e.preventDefault();
                  if (waitLegalPost) return;
                  postNewLegal();
                }}
              >
                {waitLegalPost ? "Posting..." : "Save"}
              </button>
            </form>
          </div>
          <div className="col-sm-6">
            <h5 className="light-brand-color text-center">Regulations</h5>
            {legals.map((leg) => (
              <div className="w-100 d-flex align-items-center">
                <div>
                  <span className="d-block light-brand-color">{leg.title}</span>
                  <span className="d-block">{leg.value}</span>
                </div>
                {waitLegalDelete.delete && waitLegalDelete.id === leg.id ? (
                  <span className="ml-auto text-danger icon-size fa fa-trash">
                    deleting...
                  </span>
                ) : (
                  <span
                    className="ml-auto text-danger icon-size fa fa-trash"
                    onClick={() => {
                      deleteLegal(leg.id);
                    }}
                  ></span>
                )}
              </div>
            ))}
          </div>
        </div>
      </div>
      <Terms />
      <Footer />
    </div>
  );
};

export default LegalManager;
