import Carousel from "react-elastic-carousel";
import { Link } from "react-router-dom";
import Footer from "../footer";
const breakPoints = [
  { width: 1, itemsToShow: 1 },
  { width: 550, itemsToShow: 1 },
  { width: 768, itemsToShow: 1 },
  { width: 1200, itemsToShow: 1 },
];

function HomeCarousel() {
  return (
    <div>
      <div
        className="container d-flex align-items-center"
        style={{ height: "100vh" }}
      >
        <div className="row align-items-center justify-content-around">
          <div className="col-sm-5">
            <h3 className="">
              Its not just Farming. It's a{" "}
              <span className="light-brand-color">Modern</span> farming
            </h3>
            <p>
              Modern agriculture brings enormous economic and social benefits to
              consumers including: o Improved quality of life and living
              standards as food costs decline.
            </p>
            <Link to="/services">
              <a
                href="#"
                className="btn hard-brand-background text-light display-5 font-weight-bold"
              >
                Learn more
              </a>
            </Link>
          </div>
          <div className="col-sm-5">
            <img
              src={"assets/vegetables/tomatoes_pot.png"}
              className="img-fluid"
            />
          </div>
        </div>
      </div>
      <Footer />
    </div>
  );
}

export default HomeCarousel;

