import { useEffect, useState } from "react";
import {
  deleteCommitApi,
  deleteStatsApi,
  deleteWedoApi,
  getCommitApi,
  getStatsApi,
  getWedoApi,
  postCommitApi,
  postStatsApi,
  postWedoApi,
} from "../../../api/server";
import Footer from "../../footer";
import Nav from "../nav";

export default function Index(props) {
  return (
    <div>
      <div className="" style={{ marginBottom: "4rem" }}>
        <ProjectsManage />
      </div>
      <div className="" style={{ marginBottom: "4rem" }}>
        <OurCommitment />
      </div>
      <div className="" style={{ marginBottom: "4rem" }}>
        <WhatWeDo />
      </div>
    </div>
  );
}
export function ProjectsManage(props) {
  const [wait, setWait] = useState(false);
  const [commit, setCommit] = useState({
    icon: "",
    title: "",
    countings: 0,
  });

  const [listCommit, setListCommit] = useState([]);

  const clearState = () => {
    setCommit({
      icon: "",
      title: "",
      countings: 0,
    });
  };
  const postCommit = async () => {
    try {
      if (
        Object.keys(commit).some(
          (itm) => commit[itm].trim() === "" || commit[itm].trim() === 0
        )
      ) {
        return undefined;
      }
      setWait(true);

      const resp = await postStatsApi(commit);
    } catch (e) {}
    setWait(false);
    clearState();
    getCommits();
  };
  const getCommits = async () => {
    try {
      const resp = await getStatsApi();
      // console.log(resp);
      if (resp && resp.data && resp.data.success) {
        setListCommit(resp.data.contents);
      }
    } catch (e) {
      // console.log(e);
      setListCommit([]);
    }
  };

  const deleteCommit = async (id) => {
    try {
      const resp = await deleteStatsApi(id);
      getCommits();
    } catch (e) {}
  };
  useEffect(() => {
    getCommits();
  }, []);
  return (
    <div style={{ marginTop: "5rem" }}>
      <Nav />
      <div className="container">
        <div className="row">
          <div className="col-sm-12">
            <h4 className="text-center light-brand-color">
              Project Statistics 4-parts only
            </h4>
          </div>
          <div className="col-sm-6">
            <form>
              <div className="form-group">
                <label htmlFor="exampleInputEmail1">Title</label>
                <input
                  type="text"
                  className="form-control"
                  id="exampleInputEmail1"
                  aria-describedby="emailHelp"
                  placeholder="Title"
                  value={commit.title}
                  onChange={(e) =>
                    setCommit({ ...commit, title: e.target.value })
                  }
                />
                {/* <small id="emailHelp" className="form-text text-muted">
                  We'll never share your email with anyone else.
                </small> */}
              </div>
              <div className="form-group">
                <label htmlFor="exampleInputPassword1">Brief</label>
                <input
                  type="number"
                  className="form-control"
                  id="exampleInputPassword1"
                  placeholder="Brief"
                  value={commit.countings}
                  onChange={(e) =>
                    setCommit({ ...commit, countings: e.target.value })
                  }
                />
              </div>
              {/* <div className="form-group">
                <label htmlFor="exampleInputPassword1">Read More</label>
                <input
                  type="text"
                  className="form-control"
                  id="exampleInputPassword1"
                  placeholder="Read More"
                  value={commit.readmore}
                  onChange={(e) =>
                    setCommit({ ...commit, readmore: e.target.value })
                  }
                />
              </div> */}
              <div className="form-group">
                <label htmlFor="exampleInputPassword1">icon</label>
                <input
                  type="text"
                  className="form-control"
                  id="exampleInputPassword1"
                  placeholder="Icon name"
                  value={commit.icon}
                  onChange={(e) =>
                    setCommit({ ...commit, icon: e.target.value })
                  }
                />
              </div>

              <button
                type="submit"
                className="btn text-light light-brand-background"
                onClick={(e) => {
                  e.preventDefault();
                  if (wait) return;
                  postCommit();
                }}
              >
                {wait ? "posting..." : "Save"}
              </button>
            </form>
          </div>
          <div className="col-sm-6">
            <div className="row">
              {listCommit.map((wed) => (
                <div
                  className="col-sm-6 my-2 col-xs-12 p-4 mx-auto bg-light shadow position-relative"
                  style={{ maxWidth: "15rem" }}
                >
                  <span
                    className={`d-block display-3 ${wed.icon} light-brand-color scale-up`}
                  ></span>
                  <span className="d-block light-brand-color icon-size ">
                    {wed.title}
                  </span>
                  <p className="lead">{wed.countings}</p>

                  <i
                    className="fa fa-trash icon-size text-danger position-absolute"
                    role="button"
                    onClick={() => deleteCommit(wed.id)}
                    style={{ top: "-10px", right: "-10px", zIndex: 3 }}
                  ></i>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </div>
  );
}
