import { useState, useEffect } from "react";
import { postTermApi, getTermApi } from "../../../api/server";
const Terms = (props) => {
  const [term, setTerm] = useState("");
  const [terms, setTerms] = useState([]);
  const [postWait, setPostWait] = useState(false);
  const postTerm = async () => {
    setPostWait(true);
    try {
      const resp = await postTermApi({ term });
      //   console.log(resp);
    } catch (e) {
      //   console.log(e);
    }
    getTerms(); //right the way we're updating it
    setPostWait(false);
  };
  const getTerms = async () => {
    try {
      const resp = await getTermApi(); //getting the server terms
      if (resp && resp.data.success) {
        setTerms(resp.data.payload);
      }
    } catch (e) {
      //   console.log(e);
      setTerms([]);
    }
  };
  useEffect(() => {
    getTerms(); //getting them on the page render
  }, []);
  return (
    <div className="container py-5">
      <div className="row">
        <div className="col-sm-6">
          <h5>Terms and Conditions</h5>
          <form className="">
            <div className="form-group mb-2">
              <label htmlFor="inputPassword2">Terms and Conditions</label>
              <textarea
                type="text"
                className="form-control"
                id="inputPassword2"
                placeholder="Write terms and conditions"
                value={term}
                onChange={(e) => setTerm(e.target.value)}
                row={4}
              />
            </div>

            <button
              type="submit"
              className="btn btn-success mb-2"
              onClick={(e) => {
                e.preventDefault();
                if (postWait) return;
                postTerm();
              }}
            >
              {postWait ? "Posting..." : "Save"}
            </button>
          </form>
        </div>
        <div className="col-sm-6">
          <h5 className="light-brand-color text-center">
            Current Terms and Conditions
          </h5>
          {terms.map((leg) => (
            <div className="w-100 d-flex align-items-center">
              <span className="d-block lead">{leg.term}</span>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Terms;
