export const setAdmin = async (admin) => {
  try {
    //then admin is a object
    await localStorage.setItem("@admin", JSON.stringify(admin));
  } catch (e) {
    // console.log(e)
  }
};

export const getAdmin = async () => {
  try {
    const admin = await localStorage.getItem("@admin");
    if (admin) {
      return await JSON.parse(admin);
    } else {
      return null;
    }
  } catch (e) {
    // console.log(e)
  }
};
