import { NavLink } from "react-router-dom";
import { useContext } from "react";
import { AppContext } from "../../api/store/context";
import AlternatingCircles from "../loaders/good-animations";
import { CoreValues, MileStone, OurPartners, WhatWeDo } from "./partial-knw-us";
const { FaArrowRight } = require("react-icons/fa");
const { default: Footer } = require("../footer");

const HomeSlides = (props) => {
  const { slides, waitSlides } = useContext(AppContext);
  const slidesPlate = [1, 2, 3, 4, 5, 6];
  return (
    <div className="" style={{ marginTop: "20px" }}>
      {waitSlides ? (
        <div
          style={{ height: "100vh" }}
          className="d-flex justify-content-center align-items-center"
        >
          <AlternatingCircles />
        </div>
      ) : (
        <div className="slideshow-wrapper">
          <div className="slideshow">
            {slidesPlate.map((plate) => {
              const showPlate = slides.find(
                (sl) => Number(sl.slide_no) === plate
              );

              return (
                <div className="slide">
                  <div className="content">
                    <h3>{showPlate ? showPlate.title : "TBA"}</h3>
                    <p>{showPlate ? showPlate.brief : "TBA"}</p>
                    {showPlate ? (
                      <NavLink to={`/service/${showPlate.service_id}`}>
                        <button type="button">
                          Read more
                          <i className="">
                            <FaArrowRight />
                          </i>
                        </button>
                      </NavLink>
                    ) : null}
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      )}
      <CoreValues />
      <WhatWeDo />
      <MileStone />
      <OurPartners />
      <Footer />
    </div>
  );
};

export default HomeSlides;
