import { NavLink, useParams } from "react-router-dom";
import { useState, useEffect } from "react";
import AlternatingCircles from "../loaders/good-animations";
import {
  getAllServiceCategories,
  getSingleServiceApi,
  getCategoryPointsApi,
  BASE_URL_SRC,
} from "../../api/server";
import { FaCogs } from "react-icons/fa";
const { default: Footer } = require("../footer");

const KilimoPage = (props) => {
  const [loading, setLoading] = useState(false);
  const [dataError, setDataError] = useState();
  const [service, setService] = useState(null);
  const [categories, setCategories] = useState([]);
  const { id } = useParams();

  const getSingleService = async () => {
    try {
      const response = await getSingleServiceApi(id);
      setLoading(true);
      if (response.data.contents) {
        setService(response.data.contents);
        pullCategories(response.data.contents.id);
      } else {
        setDataError("Bad URL, Follow proper navigation");
      }
      setLoading(false);
    } catch (e) {
      console.log(e);
    }
  };
  const pullCategories = async (service_id) => {
    // setLoadingCategories(true);
    try {
      const response = await getAllServiceCategories(service_id);
      if (response.data) {
        setCategories(response.data.contents);
      } else {
        setCategories([]);
      }
    } catch (e) {}
    // setLoadingCategories(false);
  };
  useEffect(() => {
    getSingleService();
  }, [id]);

  return (
    <div className="" style={{ marginTop: "70px" }}>
      {loading ? (
        <div
          className="d-flex justify-content-center align-items-center flex-column pb-5"
          style={{ height: "75vh" }}
        >
          <p className="hard-brand-color display-5 text-left py-0 my-0">
            Jadeja farming,
          </p>
          <p className="light-brand-color display-4 text-center py-0 my-0">
            Please wait...
          </p>
          {/* below couple of div is for the loading, */}
          <AlternatingCircles />
        </div>
      ) : dataError ? (
        <div
          className="d-flex justify-content-center align-items-center flex-column pb-5"
          style={{ height: "75vh" }}
        >
          <p className="light-brand-color display-4 text-center py-0 my-0">
            {dataError}
          </p>
        </div>
      ) : (
        <div>
          <div className="container py-3">
            <div className="row align-items-start">
              <div className="col-sm-9">
                <h4 className="light-brand-color display-4 light-lined-content-1">
                  {service ? service.name : ""}
                </h4>
              </div>
            </div>
          </div>
          {categories && categories.length ? (
            <div>
              {categories.map((cat, idx) => (
                <SingleHuduma
                  title={cat.title}
                  counter={idx + 1}
                  key={cat.id}
                  description={cat.description}
                  id={cat.id}
                />
              ))}
              {/* <SingleHuduma title="Vifaa Vya kilimo" counter={2} />
              <SingleHuduma title="Mbolea madawa na etc" counter={3} /> */}
            </div>
          ) : (
            <div
              className="d-flex justify-content-center align-items-center flex-column pb-5"
              style={{ height: "75vh" }}
            >
              <p className="light-brand-color display-4 text-center py-0 my-0">
                Still under construction...
              </p>
              {/* below couple of div is for the loading, */}
              <div class="cogs">
                <i className="light-brand-color display-1">
                  <FaCogs />
                </i>
              </div>
            </div>
          )}
        </div>
      )}
      <Footer />
    </div>
  );
};

export default KilimoPage;

const SingleHuduma = (props) => {
  const { title, counter, theme, description, id, key } = props; //id its a category id right now right quick
  const [points, setPoints] = useState([]);
  const [loading, setLoading] = useState(false);
  const getPointsForThisCategory = async () => {
    setLoading(true);
    try {
      const resp = await getCategoryPointsApi(id); //we pass the category id in there
      if (resp.data && resp.data.success) {
        setPoints(resp.data.contents);
      } else {
        setPoints([]);
      }
    } catch (e) {
      // console.log(e);
    }
    setLoading(false);
  };
  useEffect(() => {
    getPointsForThisCategory();
  }, []);
  return (
    <div className="container py-3">
      <div className="row">
        <div className="col-sm-12">
          <h4 className="hard-brand-color pb-2">
            <span>{counter}.</span>
            {title}
          </h4>
          <p>{description}</p>
        </div>
        {loading ? (
          <p className="display-4 light-brand-color text-center">
            Contents Loading...
          </p>
        ) : points.length ? (
          <div className="col-sm-12">
            <h5 className="light-brand-color">Soma zaidi</h5>
            <div className="row">
              {points.map((pt) => (
                <div className="col-sm-6 py-1 col-md-3">
                  <SingleCard
                    location={pt.title}
                    url={`${BASE_URL_SRC}${pt.picture}`}
                    description={pt.description}
                    id={pt.id}
                  />
                </div>
              ))}
            </div>
          </div>
        ) : null}
      </div>
    </div>
  );
};

const SingleCard = (props) => {
  const { location, url, description, id } = props;
  return (
    <div className="card" style={{ maxWidth: "18rem" }}>
      <img className="card-img-top" src={url} alt="Card image cap" />
      <div className="card-body">
        <h5 className="card-title">{location}</h5>
        <p className="card-text">
          {description ? description.substring(0, 100) + "..." : ""}
        </p>
        <NavLink to={`/soma-zaidi/${id}`}>
          <a href="#" className="btn text-light light-brand-background">
            Read more
          </a>
        </NavLink>
      </div>
    </div>
  );
};
